    <template>
    <div class="body">
        <div class="header">
            <van-nav-bar title="商品详情" left-text="返回" left-arrow @click-left="goBack">
                <template #right>
                    <van-icon @click="show.share = true" name="share" size="18" />
                </template>
            </van-nav-bar>
        </div>
        <div class="main">
            <div class="swipe">
                <van-swipe :autoplay="10000" indicator-color="black">
                    <van-swipe-item>
                        <van-image width="100%" fit="cover" :src="base + item.img" />
                        <!-- <img :src="base" alt=""> -->
                    </van-swipe-item>
                    <van-swipe-item>
                        <van-image width="100%" fit="cover" src="https://img01.yzcdn.cn/vant/cat.jpeg" />
                    </van-swipe-item>
                    <van-swipe-item>
                        <van-image width="100%" fit="cover" src="https://img01.yzcdn.cn/vant/apple-2.jpg" />
                    </van-swipe-item>
                </van-swipe>
            </div>
            <div class="item-price">
                <van-cell>
                    <span style="color: #F90909;">¥</span> <span style="font-size: 1.5rem; color: #dd3b34;">80</span>
                </van-cell>

            </div>
            <van-divider></van-divider>
            <div class="item-name">
                <van-row>
                    <van-col :span="6">
                        <div style="width: 100%;">
                            <van-tag round :type="tag" size="large">{{ tagValue }}</van-tag>
                        </div>
                    </van-col>
                    <van-col :span="15">
                        <div>
                            {{ item.name }}
                        </div>
                        <div>
                            {{ item.comment }}
                        </div>
                    </van-col>
                </van-row>


            </div>
            <div class="item-desc">
                <van-grid>
                    <van-grid-item icon="photo-o" text="文字" />
                    <van-grid-item icon="photo-o" text="文字" />
                    <van-grid-item icon="photo-o" text="文字" />
                    <van-grid-item icon="photo-o" text="文字" />
                </van-grid>
            </div>
            <div class="item-tran">
                <van-cell-group inset>
                    <van-cell icon="location-o" value="河北工业大学文创" is-link />
                    <van-cell icon="after-sale" value="7天无理由退款" is-link />
                </van-cell-group>
            </div>
            <div class="item-comments">
                <van-cell-group inset>
                    <van-cell icon="location-o" value="商品评论(5)" is-link @click="showComm" />
                    <!-- 评论区 -->
                    <van-popup v-model="showComment" position="bottom"
                        :style="{ height: '40%', 'background-color': '#f2f5f7' }">
                        <!-- <van-rate v-model="value" readonly /> -->
                        <div v-for="userComment of userComments" :key="userComment.uid">
                            <van-cell icon="user-circle-o" :title="userComment.username" :value="userComment.date"
                                size="large" :label="userComment.comment" />
                        </div>
                    </van-popup>
                </van-cell-group>
            </div>
            <br><br><br>
        </div>
        <van-divider>到底啦</van-divider>
        <div class="footer">
            <van-goods-action>
                <van-goods-action-icon @click="chat" icon="chat-o" text="客服" color="#ee0a24" />
                <van-goods-action-icon @click="goShopCar" icon="cart-o" text="购物车" />
                <van-goods-action-icon @click="collectItem" icon="star" text="已收藏" color="#ff5000" />
                <van-goods-action-button @click="addToShopCar" type="warning" text="加入购物车" />
                <van-goods-action-button type="danger" @click=onBuy text="立即购买" />
            </van-goods-action>
            <van-popup v-model="show.buy" position="bottom" :overlay="true">
                <div style="text-align: left;">
                    请选择您的收货地址
                </div>
                <div>
                    <!-- 此处的radio就是address.aid -->
                    <van-radio-group v-model="radio">
                        <van-cell-group>
                            <van-cell clickable @click="radio = address.aid" v-for="address in addressList"
                                :key="address.aid" :title="`${address.name}  ${address.tel}                      `"
                                :label="`${address.province} ${address.city} ${address.county}`">
                                <template #right-icon>
                                    <van-radio :name="address.aid" />
                                </template>
                                <!-- <van-radio :name="address.aid" /> -->
                            </van-cell>
                            <div style="border-left: 0.5px solid; border-right: 0.5px solid;">
                                <div style="border: 0.5px solid;"></div>
                                    <van-cell clickable @click="radio = 1" title-class="self-pick" title="到店自取 - 河北工业大学">
                                        <template #right-icon>
                                            <van-radio  :name="1" />
                                        </template>
                                    </van-cell>
                                <div style="border: 0.5px solid;"></div>
                            </div>
                            
                        </van-cell-group>
                    </van-radio-group>
                </div>
                <div>
                    <van-cell-group>
                        <van-field left-icon="chat-o" clearable label="备注" type="textarea" v-model="orderComment"
                            placeholder="请输入备注,最长100字" />
                    </van-cell-group>
                </div>
                <br>
                <div>
                    <van-button style="width: 100%;" type="danger" @click="submitOrder">提交订单</van-button>
                </div>
            </van-popup>
            
            <!-- 收款二维码弹框 -->
            <van-popup v-model="show.pay">
                <div>
                    <h3>
                        请支付
                    </h3>
                </div>
                <div>
                    <van-image
                        width="100%"
                        fit="cover"
                        :src="require('../assets/images/pay.jpg')"
                    />
                </div>
            </van-popup>
        </div>
    </div>
</template>
<style scoped>
.body {
    background-color: #f1f5f7;
    min-height: 100vh;
}

.header {
    position: fixed;
    width: 100%;
    z-index: 999;
}

