    color: #da3231;
    font-size: 1rem;
    margin: 0 ;
}

.footer {}
</style>
<script>

import { Toast } from 'vant';
import axios from 'axios'

const options = {
    data() {
        return {
            show: {
                share: false,
                buy: false,
                pay: false
            },
            showComment: false,
            radio: null,
            images: [
                'https://img01.yzcdn.cn/vant/apple-1.jpg',
                'https://img01.yzcdn.cn/vant/apple-2.jpg',
            ],
            userComments: [],
            tag: "",
            tagValue: "",
            item: {},
            addressList: [],
            orderComment: "",
        }
    },
    computed: {
        base() {
            return axios.defaults.baseURL;
        },
    },
    methods: {
        showPopup() {

        },
        goBack() {
            this.$router.push("/index");
        },
        showComm() {
            this.showComment = true;
        },
        chat() {

        },
        goShopCar() {
            this.$router.push("/shoppingcar");
        },
        collectItem() {

        },
        addToShopCar() {
            let form = new FormData();
            form.set("iid", this.item.iid);
            axios.post("/users/addshopcar", form).then((resp) => {
                Toast(resp.data.msg);
            })
        },
        getItemInfo() {
            let params, iid;
            try {
                params = window.location.href.split("?")[1];
                iid = params.split("=")[1];
            } catch (error) {
                Toast(error);
            }
            if (iid == undefined) {
                Toast("页面繁忙,请稍后再试!");
                return;
            }
            axios.get(`/items/info?iid=${iid}`).then((resp) => {
                this.item = resp.data.data;
            });
        },
        onBuy() {
            /**
             * 此函数在用户点击底部立即购买时触发
             * 获取地址信息, 弹出购买层
             */
            this.getAddress();
            this.show.buy = true;
        },
        getAddress() {
            axios.get("/users/address").then((resp) => {
                this.addressList = resp.data.data;
                if (resp.data.data == null) {
                    Toast("您尚未登录!");
                }
            });
        },
        submitOrder() {
            /**
             * 此函数在用户提交订单时触发
             */
            if (this.radio == null) {
                Toast("请选择收货地址!")
                return;
            }

            if (this.orderComment.length > 100) {
                Toast("备注内容过长!");
                return;
            }

            const order = {
                iid: this.item.iid,
                aid: this.radio,
                comment: this.orderComment
            }
            axios.post("/users/buy", order).then((resp) => {
                Toast(resp.data.msg);
                // 关闭提交订单页面
                this.show.buy = false;
                const _this = this;
                setTimeout(function() {
                    // 展示收款二维码
                    _this.show.pay = true;
                }, 2000);
            });
            
        },
        randomComment() {
            function generateRandomDate() {
                const start = new Date(2023, 0, 1); // Start date (January 1, 2023)
                const end = new Date(); // End date (today)
                const randomDate = new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime()));
                return randomDate.toISOString().split('T')[0];
            }

            function generateRandomUsername() {
                const usernames = ['Smith', 'Johnson', 'Brown', 'Taylor', 'Williams', 'Jones', 'Miller', 'Davis', 'García', 'Martinez'];
                const randomIndex = Math.floor(Math.random() * usernames.length);
                return usernames[randomIndex];
            }

            function generateRandomComment() {
                // const comments1 = [
                //     'Great product! I love it!',
                //     'Excellent service and quality.',
                //     'Highly recommended!',
                //     'Outstanding experience.',
                //     'I am very satisfied with my purchase.'
                // ];
                const comments = [
                    '非常棒的产品，我很喜欢！',
                    '出色的服务和质量。',
                    '强烈推荐！',
                    '卓越的体验。',
                    '我对购买非常满意。'
                ];
                const randomIndex = Math.floor(Math.random() * comments.length);
                return comments[randomIndex];
            }

            function generateRandomReviews(numReviews) {
                const reviews = [];
                for (let i = 0; i < numReviews; i++) {
                    const review = {
                        uid: i,
                        username: generateRandomUsername(),
                        date: generateRandomDate(),
                        comment: generateRandomComment()
                    };
                    reviews.push(review);
                }
                return reviews;
            }

            const numReviews = 5; // Number of reviews to generate
            const randomReviews = generateRandomReviews(numReviews);
            this.userComments = randomReviews;
        },
        randomTag() {
            const tags = ['primary', 'danger', 'success'];
            const index = Math.round(Math.random() * 100) % tags.length;
            this.tag = tags[index];
            switch (index) {
                case 0:
                    this.tagValue = "人气新品"
                    break;
                case 1:
                    this.tagValue = "爆款热卖";
                    break;
                case 2:
                    this.tagValue = "好物推荐";
                    break;
                default:
                    break;
            }
        },
    },
    mounted() {
        // 获取商品信息
        this.getItemInfo();
        // 初始化评论信息
        this.randomComment();
        // 初始化标签信息
        this.randomTag();

    }
};
export default options;
</script>